import React from 'react';

// Mui
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import Avatar from '@mui/material/Avatar';
import TwitterIcon from '@mui/icons-material/Twitter';
import InstagramIcon from '@mui/icons-material/Instagram';
import GitHubIcon from '@mui/icons-material/GitHub';
// import { Typography } from '@mui/material';

// Component
import ProfileMessage from './components/ProfileMessage';

// Media
import AvatarImage from 'media/avatar.jpg';

const Profile: React.FC = () => {
  return (
    <Box className="mainContent" id="Profile">
      <Box className="title">Profile</Box>
      <Box className="content">
        <Box className="content-box">
          <Grid container spacing={2}>
            <Grid xs={10} mdOffset="auto">
              <ProfileMessage
                message1="Web"
                message2="Java, C"
                message3="IT10"
              />
            </Grid>
            <Grid xs={10} mdOffset="auto">
              <ProfileMessage
                message1="AWS"
                message2="Docker"
                message3="IT"
              />
            </Grid>
            <Grid xs={10} mdOffset="auto">
              <ProfileMessage
                message1=""
                message2=""
                message3=""
              />
            </Grid>
            <Grid xs={10} mdOffset="auto"></Grid>
          </Grid>
          <Grid container spacing={2}>
            <Grid xs={1} mdOffset="auto">
              <Avatar
                alt="Tama"
                src={AvatarImage}
                sx={{ width: 100, height: 100 }}
              />
            </Grid>
            <Grid xs={9}>
              <Grid xs={4}>
                <TwitterIcon fontSize="large" />
                <a
                  href="https://twitter.com/tama_nattsu/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  tama_nattsu
                </a>
              </Grid>
              <Grid xs={4}>
                <InstagramIcon fontSize="large" />
                <a
                  href="https://www.instagram.com/tama_katsu13/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  tama_katsu13
                </a>
              </Grid>
              <Grid xs={4}>
                <GitHubIcon fontSize="large" />
                <a
                  href="https://github.com/k12-tamaki/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  k12-tamaki
                </a>
              </Grid>
            </Grid>
          </Grid>
        </Box>
      </Box>
    </Box>
  );
};

export default Profile;
