import React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Unstable_Grid2';
import Divider from '@mui/material/Divider';

// import IoTImage from '../../media/skill/iot.jpg';

const IoT: React.FC<{ imagePath: string }> = ({ imagePath }) => {
  return (
    <>
      <Grid container spacing={2}>
        <Grid xs={7}>
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <img style={{ maxWidth: '100%' }} src={imagePath} />
          </Box>
        </Grid>
        <Grid xs={5}>
          <Typography align="center" variant="h4" sx={{ padding: '20px' }}>
            IoT
          </Typography>
          <Divider />
          <Typography>
            IoT
          </Typography>
          <Typography>
            RaspberryPi(RevPi)AWS
            highchartsWeb
            
          </Typography>
          <Typography>
            Mailgun
          </Typography>
          <Typography>
            RaspberryPi(RevPi)
          </Typography>
          <Typography
            align="center"
            variant="h4"
            sx={{ padding: '20px', marginTop: '50px' }}
          >
            
          </Typography>
          <Divider />
          <Typography>
            AWS(EC2, RDS, S3), Python, RaspberryPi(RevPi),
          </Typography>
          <Typography>WebSocket, Vue, highcharts and more...</Typography>
        </Grid>
      </Grid>
    </>
  );
};

export default IoT;
